.logo { //     c @import "blocks/logo.scss";  style.scss
  display: grid; //   ,    .
  grid-template-columns: 34px 1fr; //           .page-header__bar,     logo.
  align-items: center; //           .page-header__logo
  gap: 16px; //              .page-header__bar

  @media (min-width: $tablet-width) {
    grid-template-columns: 60px 101px;
    gap: 12px;
  }

  @media (min-width: $desktop-width) {
    display: block;
    width: 202px;
    height: 59px;
  }
  
  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.logo__image-small {
  margin-top: -6px;
  @media (min-width: $tablet-width) {
    display: none;
  }
  @media (min-width: $desktop-width) {
    display: none;
  }
}

.logo__image-middle {
  display: none;
  @media (min-width: $tablet-width) {
    display: block;
  }
  @media (min-width: $desktop-width) {
    display: none;
  }
}


.logo__sign {

  width: 101px; 
  height: 18px;
  justify-self: center; //       
  
  @media (min-width: $tablet-width) {
    justify-self: start; //       
  }
  @media (min-width: $desktop-width) {
    display: none;
  }
}

.logo__large {
  display: none;
  @media (min-width: $desktop-width) {
    display: block;
  }
}


.page-footer__logo {
  grid-template-columns: auto;
  margin-left: auto;
  margin-right: auto;
  width: 128px;
  height: 24px;
}
