.social {
  margin-bottom: 19px;
  padding: 12px 12px; //    12
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style-type: none;

  @media (min-width: $tablet-width) {
    margin-bottom: -11px;
    margin-top: -11px;
    padding: 0;
  }
 
}

.social__item {
  margin: 0; //         
}

.social__link {
  width: 53px;
  height: 40px;
  display: grid;
  place-content: center;
  color: $special_grey-3; //            .  .   svg fill  stroke  currentColor      color: (     fill a    stroke

  @media (min-width: $tablet-width) {
    width: 52px;
    height: 52px;
    svg {
      transform: scale(1.43);
    }
  }

  &:hover {
    color: $color-primary; //   svg fill  stroke  currentColor      color: (     fill a    stroke
  }

  &:active {
    color: fade-out($color-primary, 0.7);
  }
}
